<template>
    <div ref="experience" id="experience" :style="{paddingTop:navbarHeight+'px'}">
        <section class="education container pb-5">
            <div class="text-center py-5">
                <h2>Experience</h2>
            </div>

            <div class="list-infograph py-3">
                <div :class="['infograph', startTransition ? 'transition' : '']" v-for="(v,i) in experience" :key="i">
                    <div class="mb-4">
                        <img class="company-logo" :src="v.image" alt="company-logo">
                    </div>
                    <h4 class="title">{{ v.company }}</h4>
                    <p><b>{{ v.time }}</b></p>
                    <div>{{ v.department }}</div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import { mapState } from 'pinia'
import { globalStore } from '@/stores/globalStore'

export default {
    name: "Experience",
    data() {
        return {
            offsetTop: 0,
            offsetBottom: 0,
            startTransition: false,
            experience: [
                {
                    image: require('../assets/image/logo_walline.png'),
                    company: 'Walline Facade Wallart',
                    time: '2017 - 2018',
                    department: 'Graphic design'
                },
                {
                    image: require('../assets/image/logo_alamraya.svg'),
                    company: 'Alamraya Sebar barokah',
                    time: '2019 - 2020',
                    department: 'Laravel developer'
                },
                {
                    image: require('../assets/image/logo_redaco.svg'),
                    company: 'Redaco System Factory',
                    time: '2020 - 2023',
                    department: 'Front-end Web Developer'
                },
            ]
        }
    },
    watch: {
        scroll(value) {
            if(value >= this.offsetTop && value<= this.offsetBottom && !this.startTransition) {
                setTimeout(() =>{
                    this.startTransition = true
                }, 10)
            }
        }
    },
    computed: {
        ...mapState(globalStore, ['scroll', 'navbarHeight'])
    },
    mounted() {
        this.offsetTop = this.$refs.experience.offsetTop
        this.offsetBottom = this.offsetTop + this.$refs.experience.offsetHeight
    }
}
</script>

<style scoped lang="scss">
@import '../assets/css/experience.scss';
</style>