<template>
<div ref="about" id="about" :style="{paddingTop:navbarHeight+'px'}">
    <section class="container pb-5">
        <div class="text-center py-5">
            <h2>About</h2>
        </div>
        <div class="row">
            <div class="col-lg-6 left-col" :style="startTransition ? transition[0] : null">
                <h5>Mohammad Zakaria</h5>
                <p>nama saya moh.zakaria biasa dipanggil zaka saya alumni dari SMKN 1 Driyorejo jurusan Teknik Elektronika Industri tahun 2017</p>
                <div class="d-flex justify-content-between border-bottom py-2" v-for="(v,i) in info" :key="i">
                    <div>{{ v.name }}</div>
                    <div>{{ v.value }}</div>
                </div>
            </div>
            <div class="col-lg-5 offset-lg-1 right-col">
                <div class="my-2" v-for="(v,i) in skills" :key="i">
                    <div class="d-flex justify-content-between mb-2">
                        <div>
                            {{ v.name }}
                        </div>
                        <div>
                            {{ (startTransition ? v.percentage : '0') + '%' }}
                        </div>
                    </div>
                    <div class="progress">
                        <div class="progress-bar" role="progressbar" :style="{ width: (startTransition ? v.percentage : '0') + '%'}" aria-valuenow="80" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</div>
</template>

<script>
import { globalStore } from '@/stores/globalStore';
import { mapState } from 'pinia';

export default {
    name: "About",
    data() {
        return {
            offsetTop: 0,
            offsetBottom: 0,
            startTransition: false,
            transition: [
                {
                    opacity: 1,
                    right: 0
                },
            ],
            info: [
                {
                    name: 'Email:',
                    value: 'mzakaria1804@gmail.com'
                },
                {
                    name: 'Hp/Whatsapp',
                    value: '085735596113'
                }
            ],
            
            skills: [
                {
                    name: 'Laravel',
                    percentage: 90
                },
                {
                    name: 'Codeigniter',
                    percentage: 70
                },
                {
                    name: 'SASS',
                    percentage: 80
                },
                {
                    name: 'Vue',
                    percentage: 80
                }
            ]
        }
    },
    watch: {
        scroll(value) {
            if(value >= this.offsetTop && value <= this.offsetBottom && !this.startTransition) {
                setTimeout(() =>{
                    this.startTransition = true
                }, 10)
            }
        }
    },
    computed:{
        ...mapState(globalStore, ['scroll', 'navbarHeight'])
    },
    mounted() {
        this.offsetTop = this.$refs.about.offsetTop
        this.offsetBottom = this.offsetTop + this.$refs.about.offsetHeight
    }
}
</script>

<style scoped lang="scss">
@import '../assets/css/about.scss';
</style>