<template>
    <div class="navbar-custom" ref="navbar-custom">
        <nav class="navbar navbar-expand-sm navbar-light">
            <div class="navbar-brand" href="#">{{ time }}</div>

            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbar">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbar">
                <ul class="navbar-nav ms-auto">
                    <li class="nav-item" v-for="(v,i) in navItem" :key="i"><a class="nav-link" @click="removeHash" :href="v.url" id="abo">{{ v.text }}</a></li>
                </ul>
            </div>
        </nav>
    </div>
</template>

<script>
import { mapActions } from 'pinia'
import { globalStore } from '@/stores/globalStore.js'
import moment from 'moment'

export default {
    name:"navbar",
    data() {
        return {
            time: '',
            navItem:[
                {
                    text: 'ABOUT',
                    url: '#about'
                },
                {
                    text: 'EXPERIENCE',
                    url: '#experience'
                }
            ]
        }
    },
    methods: {
        ...mapActions(globalStore, ['setNavbarHeight']),
    },
    mounted() {
        this.setNavbarHeight(this.$refs['navbar-custom'].getBoundingClientRect().height)
        
        setInterval(() => {
            this.time = moment().format('dddd HH:mm:ss')
        }, 1000)
    }
}
</script>

<style scoped lang="scss">
    @import '../assets/css/navbar.scss';
</style>